.download-button {
    position: relative;
    width: 220px;
    height: 60px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border: 1px solid #ffb912;
    background-color: transparent;
    overflow: hidden;
    border-radius: 35px;
    font-family: "Poppins", sans-serif;
  }
  
  .download-button, .button__icon, .button__text {
    transition: all 0.3s;
  }
  
  .download-button .button__text {
    transform: translateX(22px);
    color: #fff;
    font-weight: 600;
  }
  
  .download-button .button__icon {
    position: absolute;
    transform: translateX(159px);
    height: 100%;
    width: 60px;
    background-color: #ffb912;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 22px; */
    border-radius: 35px;
  }
  
  .download-button .svg {
    width: 20px;
    fill: #fff;
  }
  
  .download-button:hover {
    background: #ffb912;
  }
  
  .download-button:hover .button__text {
    color: transparent;
  }
  
  .download-button:hover .button__icon {
    width: 220px;
    transform: translateX(0);
  }
  
  .download-button:active .button__icon {
    background-color: #ffb912;
  }
  
  .download-button:active {
    border: 1px solid #ffb912;
  }
